import { createNamespacedHelpers } from "vuex";
import moment from "moment";

const { mapState, mapActions } = createNamespacedHelpers("global");

export default {
  data() {
    return {
      selected: [],
      pagination: {
        sortBy: "createdDate",
        descending: true
      },
      rowsPerPageItems: [
        10,
        20,
        30,
        { text: this.$t("message.all"), value: -1 }
      ],
      headers: [
        {
          text: this.$t("message.createdDate"),
          value: "createdDate",
          class: "header"
        },
        {
          text: this.$t("message.applicant"),
          value: "applicant",
          class: "header"
        },
        {
          text: this.$t("message.startDate"),
          value: "startDate",
          class: "header"
        },
        {
          text: this.$t("message.endDate"),
          value: "endDate",
          class: "header"
        },
        {
          text: this.$t("message.unavailability_reason"),
          value: "unavailabilityWording",
          class: "header"
        }
      ]
    };
  },
  methods: {
    changeSort(column) {
      if (this.pagination.sortBy === column) {
        this.pagination.descending = !this.pagination.descending;
      } else {
        this.pagination.sortBy = column;
        this.pagination.descending = false;
      }
    },
    refuseSelected(){
      this.selected.forEach(unavailability=>{
        this.refuseUnavailabilities(unavailability).then(v=>{
          const index = this.selected.indexOf(unavailability);
          this.selected.splice(index,1)
        });
      })
    },
    validateSelected(){
      this.selected.forEach(unavailability=>{
        this.validateUnavailabilities(unavailability).then(v=>{
          const index = this.selected.indexOf(unavailability);
          this.selected.splice(index,1)
        });
      })
    },
    ...mapActions(["getUnavailabilitiesToValidate","validateUnavailabilities","refuseUnavailabilities"])
  },
  filters: {
    formatDDMMYYYYHHmm(t) {
      return moment(t).format("DD/MM/YYYY HH:mm");
    },
    formatDDMMYYYY(t) {
      return moment(t).format("DD/MM/YYYY");
    }
  },
  mounted() {
    this.getUnavailabilitiesToValidate();
  },
  computed: {
    ...mapState({
      listUnavailabilityToValidate: state => state.unavailabilitiesToValidate
    })
  }
};



