<template>
	<v-menu
        v-model="showUserInformationsMenu"
        offset-y absolute
        :position-y="y"
				:position-x="x"
        bottom
        origin="center center"
        transition="slide-x-transition"
        :close-on-content-click="false"
    >
    <v-card class="menu-user">
      <v-card-title class="title-menu-user">
          <span class="title-center"><v-icon  class="mr-2" style="">account_circle</v-icon> {{user.nom}} {{user.prenom}}</span>
      </v-card-title>
      <v-divider></v-divider>
      <v-card-text>
        <p>matricule : <b>{{user.matricule}}</b></p>
        <p>
          responsable :
					<b>{{manager.nom+' '+manager.prenom}}</b>
				</p>
				<div v-for="number in user.telephone" :key="number.id">
					<v-icon  class="mr-2" style="">phone</v-icon> {{':  ' + number}}
				</div>
      </v-card-text>
    </v-card>
  </v-menu>
</template>
<script>
import { createNamespacedHelpers } from "vuex";

const { mapGetters } = createNamespacedHelpers("global");

export default {
  name: "UserInformationsMenu",
  data: () => ({
    y: 0,
    x: 0,
    showUserInformationsMenu: false,
    user: {},
    manager: {}
  }),
  computed: {
    ...mapGetters(["getManager"]),
    filteredTelephone() {
      if (this.telephone === null) return null;
      return this.telephone.split(",");
    }
  },
  mounted() {
    this.$root.$on("show-menu-user", (showUserInformationsMenu, item, y, x) => {
      this.showUserInformationsMenu = showUserInformationsMenu;
      this.manager = { prenom: item.prenom_n1, nom: item.nom_n1 };
      this.user = {
        nom: item.nom,
        prenom: item.prenom,
        matricule: item.matricule,
        gid_collaborateur: item.gid_collaborateur,
        telephone: item.telephone
      };
      this.y = y + 15;
      this.x = x - 150;
    });
  }
};
</script>

<style>
.menu-user {
  width: 300px !important;
}
.title-menu-user {
  background-color: #ff9800;
  color: white;
  font-size: 11px;
}

.title-center {
  margin: auto;
}

.text-menu-user {
  font-size: 14px;
  text-align: left;
  margin: auto;
}

.text-menu-user-center {
  font-size: 14px;
  text-align: center;
  margin: auto;
}
</style>



