<template>
  <v-container>
    <v-layout row wrap>
      <v-list dense>

        <h1 inset> {{$t("message.status2")}}</h1>

        <v-list-tile 
          v-for="item in status"
          avatar
          :key="item.code_color"
        >
          <v-list-tile-avatar>
            <v-icon :class="[item.code_color,'opacity']"></v-icon>
          </v-list-tile-avatar>
          <v-list-tile-content>
            <v-list-tile-title v-html="item.status_title"></v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>

        <h1 inset> {{$t("message.unavailability_reasons")}}</h1>
        <template  v-for="(unavailability_reason,index) in unavailability_reasons">
          <v-list-tile            
            avatar
            :key="unavailability_reason.code_color"
          >
            <v-list-tile-avatar>
              <v-icon :class="[unavailability_reason.code_color,'opacity']"></v-icon>
            </v-list-tile-avatar>
            <v-list-tile-content>
              <v-list-tile-title v-html="unavailability_reason.lib_abs_non_affectee"></v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile> 
          <v-divider :key="index"></v-divider>
        </template>         
        </v-list>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  name: "Legends",
  data() {
    return {
      status: [
        {
          code_color: "waiting",
          status_title: this.$t("message.waiting")
        }
      ],
      unavailability_reasons: [
        {
          code_color: "unavailabilities_reason_AAB",
          code_abs_non_affectee: "AAB",
          lib_abs_non_affectee: this.$t("message.other_absences")
        },
        {
          code_color: "unavailabilities_reason_ATR",
          code_abs_non_affectee: "ATR",
          lib_abs_non_affectee: this.$t("message.work_accident")
        },
        {
          code_color: "unavailabilities_reason_CLI",
          code_abs_non_affectee: "CLI",
          lib_abs_non_affectee: this.$t("message.customer_relationship")
        },
        {
          code_color: "unavailabilities_reason_CNG",
          code_abs_non_affectee: "CNG",
          lib_abs_non_affectee: this.$t("message.leave")
        },
        {
          code_color: "unavailabilities_reason_DLG",
          code_abs_non_affectee: "DLG",
          lib_abs_non_affectee: this.$t("message.delegation")
        },
        {
          code_color: "unavailabilities_reason_ECT",
          code_abs_non_affectee: "ECT",
          lib_abs_non_affectee: this.$t("message.framing")
        },
        {
          code_color: "unavailabilities_reason_FEP",
          code_abs_non_affectee: "FEP",
          lib_abs_non_affectee: this.$t("message.public_presence")
        },
        {
          code_color: "unavailabilities_reason_FER",
          code_abs_non_affectee: "FER",
          lib_abs_non_affectee: this.$t("message.holidays")
        },
        {
          code_color: "unavailabilities_reason_FRM",
          code_abs_non_affectee: "FRM",
          lib_abs_non_affectee: this.$t("message.training")
        },
        {
          code_color: "unavailabilities_reason_GES",
          code_abs_non_affectee: "GES",
          lib_abs_non_affectee: this.$t("message.management")
        },
        {
          code_color: "unavailabilities_reason_MAL",
          code_abs_non_affectee: "MAL",
          lib_abs_non_affectee: this.$t("message.disease")
        },
        {
          code_color: "unavailabilities_reason_REU",
          code_abs_non_affectee: "REU",
          lib_abs_non_affectee: this.$t("message.meeting")
        },
        {
          code_color: "unavailabilities_reason_THR",
          code_abs_non_affectee: "THR",
          lib_abs_non_affectee: this.$t("message.hours_recovery")
        }
      ]
    };
  }
};
</script>

<style>
.legends {
  width: 30px;
  height: 30px;
  border-radius: 5px;
  opacity: 0.6;
}
.legends-text {
  font-size: 14px;
  padding: 5px;
}
</style>



