import moment from "moment";
import { createNamespacedHelpers } from "vuex";
import Events from "../Events";

const { mapActions, mapState } = createNamespacedHelpers("global");

export default {
  data() {
    return {
      choiceUnavailability: null,
      choiceHour: null,
      toggle_one: 0,
      radioGroupHalfDay: 0,
      radioGroupPeriodStart: 0,
      radioGroupPeriodEnd: 0,
      start_date_menu: null,
      start_time_menu: null,
      end_date_menu: null,
      end_time_menu: null,
      unavailabilityReasonsModel: null,
      familyUnavailabilityReasonsModel: null,
      unavailabilityReasonsItems: [],
      start_date: null,
      end_date: null,
      start_time: null,
      end_time: null,
      disabled_morning: false,
      disabled_morning_period: false,
      reasonBalance: false,
      reasonProof: false,
      reasonValidation: false
    };
  },
  mounted() {
    this.getUnavailabilityReasonsForMe();
  },
  computed: {
    validated() {
      return !(
        this.unavailabilityReasonsModel &&
        this.start_time &&
        this.start_date &&
        this.end_time &&
        this.end_date
      );
    },
    start_date_fr() {
      return moment(this.start_date).format("DD/MM/YYYY");
    },
    end_date_fr() {
      return this.end_date ? moment(this.end_date).format("DD/MM/YYYY") : null;
    },
    min_start_date() {
      return moment().format("YYYY-MM-DD");
    },
    min_start_date_day() {
      return moment()
        .add(1, "days")
        .format("YYYY-MM-DD");
    },
    min_end_date() {
      const todayDate = moment().format("YYYY-MM-DD");
      const min_end_date_value = moment(this.start_date).isBefore(todayDate)
        ? todayDate
        : this.start_date;
      return this.choiceUnavailability === "PERIOD"
        ? moment(this.start_date)
            .add(1, "days")
            .format("YYYY-MM-DD")
        : min_end_date_value;
    },
    min_end_time() {
      const todayDate = moment().format("YYYY-MM-DD");
      const todayTime = moment().format("HH:mm");

      if (
        moment(this.end_date).isSame(todayDate) &&
        moment(this.start_date).isSame(todayDate)
      )
        return moment(todayTime, "HH:mm").isBefore(
          moment(this.start_time, "HH:mm")
        )
          ? this.start_time
          : todayTime;

      if (moment(this.start_date).isSame(this.end_date)) return this.start_time;

      if (moment(this.end_date).isSame(todayDate)) return todayTime;

      return null;
    },
    ...mapState({
      familyUnavailabilityReasons: state =>
        state.family_unavailability_reasons_for_me
    })
  },
  methods: {
    initValue() {
      // Motif à l'initialisation
      this.udapteFamilyUnavailabilityReason();
      this.familyUnavailabilityReasonsModel = null;
      this.unavailabilityReasonsModel = null;
      this.choiceHour = null;
      // Choix à l'initialisation : Demi-Journée
      this.initChoiceHalfDay();
      this.toggle_one = 0;

      // Dates à l'initialisation
      this.start_date = moment().format("YYYY-MM-DD");
      this.end_date = moment().format("YYYY-MM-DD");
    },
    udapteUnavailabilityReason() {
      if (this.unavailabilityReasonsModel) {
        this.reasonBalance = this.unavailabilityReasonsModel.solde;
        this.reasonProof = this.unavailabilityReasonsModel.justificatif;
        this.reasonValidation = this.unavailabilityReasonsModel.validation;
      } else {
        this.reasonBalance = false;
        this.reasonProof = false;
        this.reasonValidation = false;
      }
    },
    udapteFamilyUnavailabilityReason() {
      if (this.familyUnavailabilityReasonsModel) {
        this.unavailabilityReasonsItems = this.$store.state.global.unavailability_reasons_for_me.filter(
          unavailabilityReason =>
            unavailabilityReason.code_abs_non_affectee ===
            this.familyUnavailabilityReasonsModel
        );
      } else {
        this.unavailabilityReasonsItems = this.$store.state.global.unavailability_reasons_for_me;
      }
    },
    showChoice() {
      if (this.choiceUnavailability === "DAY") {
        this.end_date = this.start_date;
      } else if (this.choiceUnavailability === "HALFDAY") {
        this.hourHalfDay(this.radioGroupHalfDay);
        this.end_date = this.start_date;
      } else if (this.choiceUnavailability === "PERIOD") {
        this.hourHalfDayStart(this.radioGroupPeriodStart);
        this.hourHalfDayEnd(this.radioGroupPeriodEnd);
      }
    },
    showHour(booleanHour) {
      this.choiceHour = booleanHour;
      if (this.choiceHour !== null) this.initChoicePeriodHour();
    },
    hourHalfDay(halfDay) {
      // Adaptation de l'heure avec la demi-journée sélectionné
      // Si l'après midi est sélectionné
      if (halfDay) {
        this.start_time = "12:00";
        this.end_time = "24:00";
      } else {
        this.start_time = "00:00";
        this.end_time = "12:00";
      }
    },
    hourHalfDayStart(halfDay) {
      // Adaptation de l'heure avec la demi-journée sélectionné
      // Si l'après midi est sélectionné
      if (halfDay) {
        this.start_time = "12:00";
      } else {
        this.start_time = "00:00";
      }
    },
    hourHalfDayEnd(halfDay) {
      // Adaptation de l'heure avec la demi-journée sélectionné
      // Si l'après midi est sélectionné
      if (halfDay) {
        this.end_time = "24:00";
      } else {
        this.end_time = "12:00";
      }
    },
    initChoiceHalfDay() {
      this.choiceUnavailability = "HALFDAY";
      // Initialisation de la date
      this.start_date = moment().format("YYYY-MM-DD");
      this.end_date = moment().format("YYYY-MM-DD");

      // Heures et bouton radio à l'initialisation selon l'heure actuelle
      const hourNow = moment().format("a");
      // Si am
      if (hourNow === "am") {
        this.radioGroupHalfDay = 0;
        this.disabled_morning = false;
      } else {
        this.radioGroupHalfDay = 1;
        this.disabled_morning = true;
      }
      this.hourHalfDay(this.radioGroupHalfDay);
    },
    initChoiceDay() {
      this.choiceUnavailability = "DAY";

      // Initialisation de la date à demain
      this.start_date = moment()
        .add(1, "days")
        .format("YYYY-MM-DD");
      this.end_date = moment()
        .add(1, "days")
        .format("YYYY-MM-DD");

      this.start_time = "00:00";
      this.end_time = "24:00";
    },
    initChoiceHour() {
      this.choiceUnavailability = "HOUR";
      // Initialisation de la date
      this.start_date = moment().format("YYYY-MM-DD");
      this.end_date = moment().format("YYYY-MM-DD");

      this.start_time = "00:00";
      this.end_time = "24:00";
    },
    initChoicePeriod() {
      this.choiceUnavailability = "PERIOD";
      // Initialisation de la date
      this.start_date = moment().format("YYYY-MM-DD");
      this.end_date = moment()
        .add(1, "days")
        .format("YYYY-MM-DD");

      // Initialisation des heures pour PERIOD
      const hourNow = moment().format("a");
      // Si am
      if (hourNow === "am") {
        this.radioGroupPeriodStart = 0;
        this.disabled_morning_period = false;
      } else {
        this.radioGroupPeriodStart = 1;
        this.disabled_morning_period = true;
      }
    },
    initChoicePeriodHour() {
      // Adaptation avec le composant de l'heure (pas de 15 minutes à la demande du client)
      // L'instant t
      const now = new Date();
      // Initialisation des secondes à zéro
      now.setSeconds(0);
      // Initialisation des millisecondes à zéro
      now.setMilliseconds(0);
      // Récupère les minutes de l'instant t
      const minutes = now.getMinutes();
      // Initialisation de la variable d'ajout des minutes
      let minutesToAdd = 0;
      // Si les minutes de l'instant t est supérieur ou égale à zéro et inférieur ou égale à 14
      if (minutes >= 0 && minutes <= 14) {
        // Ajout de minute est égale à 30 - minute à l'instant t
        minutesToAdd = 15 - minutes;
      } else if (minutes >= 16 && minutes <= 29) {
        minutesToAdd = 30 - minutes;
      } else if (minutes >= 31 && minutes <= 44) {
        minutesToAdd = 45 - minutes;
      } else if (minutes >= 46 && minutes <= 59) {
        minutesToAdd = 60 - minutes;
      }
      // Minute en milliseconde
      const MIN_IN_MS = 60000;
      // quart-heure en milleseconde
      const FIFTEEN_IN_MS = 900000;

      minutesToAdd *= MIN_IN_MS;
      const startDateTime = new Date(now.getTime() + minutesToAdd);
      const endDateTime = new Date(startDateTime.getTime() + FIFTEEN_IN_MS);

      this.start_time = moment(startDateTime).format("HH:mm");
      this.end_time = moment(endDateTime).format("HH:mm");
    },
    saveDateHour(menu, date) {
      // On sauvegarde la valeur donnée
      this.$refs[menu].save(date);
      // On vérifie s'il n'y a pas des conflits entre les valeurs
      this.checkInputs();
    },
    checkInputs() {
      // Si la date de début est supérieur à la date de début min
      if (moment(this.start_date).isAfter(this.min_start_date)) {
        this.disabled_morning = false;
        this.disabled_morning_period = false;
      } else {
        this.disabled_morning = true;
        this.disabled_morning_period = true;
      }

      // Fonction de gestion des conflits de dates
      if (moment(this.end_date).isBefore(this.min_end_date))
        if (this.choiceUnavailability === "PERIOD")
          this.end_date = moment(this.start_date)
            .add(1, "days")
            .format("YYYY-MM-DD");
        else this.end_date = this.start_date;

      if (
        moment(this.end_date).isSame(this.min_end_date) &&
        moment(this.end_time, "HH:mm").isBefore(
          moment(this.min_end_time, "HH:mm")
        )
      )
        this.end_time = this.min_end_time;
    },
    ...mapActions(["getUnavailabilityReasonsForMe", "saveUnavailability"]),
    leave() {
      this.$root.$emit(Events.ADD_INDISPO_LEAVE);
    },
    showAlert() {
      // Gestion des choix
      this.showChoice();

      const todayDate = moment(); // Date du jour
      const dateTimeFin = moment(`${this.end_date} ${this.end_time}`); // Date de fin

      // Test si au moment de la validation la date de fin est dans le passé
      if (todayDate.isBefore(dateTimeFin)) {
        this.saveUnavailability({
          indisponibilite: this.unavailabilityReasonsModel.code_abs,
          start_date: this.start_date,
          end_date: this.end_date,
          start_time: this.start_time,
          end_time: this.end_time
        }).then(
          // On demande a CalendarView de recharger les indispos
          () => this.leave()
        );
        this.closeDialog();
        this.$swal(this.$t("message.finish"), this.messageFinish(), "success");
      } else {
        this.$swal(
          this.$t("message.error"),
          `${this.$t("message.error_date")}(${dateTimeFin.format(
            "DD-MM-YYYY HH:mm"
          )}).
          `,
          "error"
        );
      }
    },
    messageFinish() {
      let messageFinishValue = "";

      if (this.reasonProof || this.reasonValidation) {
        messageFinishValue = `<div class="alert error">
        <i aria-hidden="true" class="icon material-icons alert__icon">info</i>`;
      }

      if (this.reasonProof) {
        messageFinishValue = `${messageFinishValue}${this.$t(
          "message.reason_proof"
        )}`;
      }

      if (this.reasonValidation) {
        if (this.reasonProof) {
          messageFinishValue = `${messageFinishValue}<br/>`;
        }
        messageFinishValue = `${messageFinishValue}${this.$t(
          "message.reason_validation"
        )}`;
      }

      if (this.reasonProof || this.reasonValidation) {
        messageFinishValue = `${messageFinishValue}</div>`;
      }

      // Retourne le message finale
      return `${messageFinishValue}
      <div>
        ${this.$t("message.finish_request")}<br/>
            <span style="font-weight: bold">
              ${moment(this.start_date).format("DD/MM/YYYY")} ${
        this.start_time
      } 
              ${this.$t("message.the")} 
              ${moment(this.end_date).format("DD/MM/YYYY")} ${this.end_time}
            </span>
          <br/>
        ${this.$t("message.finish_request_save")}
      </div>`;
    },
    closeDialog() {
      this.$emit("exit", false);
      // Clear de l'indisponibilite
      this.unavailabilityReasonsModel = null;
      this.familyUnavailabilityReasonsModel = null;
    }
  }
};



