import { createNamespacedHelpers } from "vuex";
import moment from "moment";

const { mapState, mapActions } = createNamespacedHelpers("global");

export default {
  data() {
    return {
      selectedUnavailability: null,
      showValidator: null,
      x: null,
      y: null,
      pagination: { sortBy: "createdDate", descending: true },
      rowsPerPageItems: [
        10,
        20,
        30,
        { text: this.$t("message.all"), value: -1 }
      ],
      headers: [
        {
          text: this.$t("message.createdDate"),
          value: "createdDate",
          class: "header"
        },
        {
          text: this.$t("message.startDate"),
          value: "startDate",
          class: "header"
        },
        { text: this.$t("message.endDate"), value: "endDate", class: "header" },
        {
          text: this.$t("message.unavailability_reason"),
          value: "unavailabilityWording",
          class: "header"
        },
        { text: this.$t("message.status"), value: "statut", class: "header" }
      ]
    };
  },
  methods: {
    ...mapActions(["getEmployeesUnavailabilities", "loadEmployeeFromGid"]),
    displayValidator(item, event) {
      if (item.gid_modification) {
        this.loadEmployeeFromGid(item.gid_modification);
        this.x = event.clientX-250;
        this.y = event.clientY+15;
        this.selectedUnavailability = item;
        this.showValidator = true;
      }
    },
    changeSort(column) {
      if (this.pagination.sortBy === column) {
        this.pagination.descending = !this.pagination.descending;
      } else {
        this.pagination.sortBy = column;
        this.pagination.descending = false;
      }
    }
  },
  filters: {
    formatDDMMYYYYHHmm(t) {
      return moment(t).format("DD/MM/YYYY HH:mm");
    },
    formatDDMMYYYY(t) {
      return moment(t).format("DD/MM/YYYY");
    }
  },
  mounted() {
    this.getEmployeesUnavailabilities();
  },
  computed: {
    ...mapState({
      listUnavailabilityUser: state => state.unavailabilities,
      validator: state => state.validator
    }),
    validatorCardText() {
      if (this.selectedUnavailability && this.validator.nom)
        return `${
          this.selectedUnavailability.statut === "VALIDEE"
            ? this.$t("message.valided")
            : this.$t("message.refused")
        } ${this.$t("message.The")} ${
          moment(this.selectedUnavailability.date_modification).format("DD/MM/YYYY à HH:mm")
        } ${this.$t("message.by")} ${this.validator.prenom} ${this.validator.nom}`;
      return "";
    }
  }
};



