<template>
  <v-content style="margin:inherit; width:100%; padding:inherit">
    <v-tabs color="grey" dark  slider-color="warning" v-model="tab">
      <!-- First tab -->
      <v-tab ripple @click="loadCalendar">
        {{$t("message.calendar")}}
      </v-tab>

      <!-- Second tab -->
      <v-tab ripple @click="loadDataTableEmployeesUnavailabilities">
        <v-badge color="warning" right >{{$t("message.basket")}}</v-badge>
      </v-tab>

      <!-- Third tab -->
      <v-tab v-if="isN1" ripple @click="loadDataTableUnavailabilitiesToValidate">
        <v-badge v-model="showBadge" color="warning" right >{{$t("message.validations")}}<span slot="badge">{{ numberValidation }}</span></v-badge>
      </v-tab>
    </v-tabs>
    <v-tabs-items :touchless="true" v-model="tab">
    <v-tab-item>
      <v-flex md-12>
        <v-card class="pb-5">
          <v-card-title class="subtitle" >
            <v-btn small color="primary" @click.stop="showDialog"> <v-icon class="  mr-2">add</v-icon>{{$t("message.declared")}}</v-btn>
            <v-btn small color="primary" @click.stop="showStandbyTime"> <v-icon  v-if="!visibility" class="mr-2">visibility_off</v-icon> <v-icon v-else class="mr-2">visibility</v-icon>{{$t("message.standbyTime")}}</v-btn>
            <v-btn small color="primary" @click.stop="drawer = !drawer"> <v-icon class="  mr-2">account_circle</v-icon>{{$t("message.search")}}</v-btn>
           <v-btn small color="primary" @click.stop="drawerL = !drawerL"> <v-icon class="  mr-2">help_outline</v-icon>{{$t("message.legend")}}</v-btn>
          </v-card-title>
          <v-divider></v-divider>
          <Calendar ref="childCalendar" :show-date="showDate"
          @show-date-change="setShowDate"
          class="mt-4 mx-4 theme-default"
          :startingDayOfWeek="1"
          :display-period-uom="displayPeriodUom"
          />
        </v-card>
      </v-flex>
    </v-tab-item>
    <v-tab-item>
      <v-flex md-12>
        <v-card class="pb-5">
          <v-card-title class="subtitle" >
            <v-icon class="roundicon  mr-2">shopping_basket</v-icon> {{$t("message.basket_list")}}
            <v-spacer></v-spacer>
            <v-btn small color="primary" @click.stop="showDialog"> <v-icon class="  mr-2">add</v-icon>{{$t("message.declared")}}</v-btn>
          </v-card-title>
            <ListUnavailabilityUser ref="childDatatable"></ListUnavailabilityUser>
        </v-card>
      </v-flex>
    </v-tab-item>
    <v-tab-item>
        <v-flex md-12>
          <v-card class="pb-5">
            <v-card-title class="subtitle" >
              <v-icon class="roundicon  mr-2">verified_user</v-icon> {{$t("message.validations_list")}}
            </v-card-title>
              <ListUnavailabilityManager ref="childToValidate"></ListUnavailabilityManager>
          </v-card>
        </v-flex>
    </v-tab-item>
  </v-tabs-items>
    <v-dialog persistent
    v-model="dialog"
    max-width="600"
    >
      <AddIndispo @exit="closeDialog" ref="childAddIndispo"></AddIndispo>
    </v-dialog>
    <!--  Menu recherche perimetre/user droite -->
    <v-navigation-drawer
      v-model="drawer"
      app
      temporary
      right
      hide-overlay
      touchless
      width="400"
      style="width:400px !important"
    >
      <div style="background-color:#FF9800; height:64px; color:white; padding:10px;">
        <v-icon dark class="mr-2" style="font-size:30px">account_circle</v-icon> <span>{{$t("message.perimeter")}}</span>
      </div>
      <div>
        <search-calendar></search-calendar>
      </div>
    </v-navigation-drawer>
    <v-navigation-drawer
      v-model="drawerL"
      app
      temporary
      right
      hide-overlay
      touchless
    >
      <div style="background-color:#FF9800; height:64px; color:white; padding:10px;">
        <v-icon dark class="mr-2" style="font-size:30px">help_outline</v-icon> <span>{{$t("message.legend")}}</span>
      </div>
      <div>
        <Legends></Legends>
      </div>
    </v-navigation-drawer>
  </v-content>
</template>

<script>
import moment from "moment";
import Calendar from "@/components/Calendar/CalendarView";
import AddIndispo from "@/components/AddIndispo";
import ListUnavailabilityUser from "@/components/ListUnavailabilityUser";
import ListUnavailabilityManager from "@/components/ListUnavailabilityManager";
import searchCalendar from "@/components/searchCalendar/searchCalendarView";
import Legends from "@/components/Legends";
import { createNamespacedHelpers } from "vuex";

const namespacedHelpersUser = createNamespacedHelpers("user");
const mapGettersUser = namespacedHelpersUser.mapGetters;

const { mapState } = createNamespacedHelpers("global");

export default {
  name: "Default",
  data() {
    return {
      drawer: null,
      drawerL: null,
      showDate: new Date(),
      displayPeriodUom: "week",
      dialog: false,
      start_date: moment().format("YYYY-MM-DD"),
      end_date: moment().format("YYYY-MM-DD"),
      start_time: moment().format("HH:mm"),
      end_time: moment().format("HH:mm"),
      afficherPerimetres: false,
      min_start_date: moment().format("YYYY-MM-DD"),
      min_start_date_day: moment()
        .add(1, "days")
        .format("YYYY-MM-DD"),
      min_end_date: moment().format("YYYY-MM-DD"),
      min_end_time: moment().format("HH:mm"),
      visibility: true,
      tab: null,
      showBadge: false
    };
  },
  created() {
    // Charge le gid depuis le localstorage dans le state du module user une fois pour toutes au démarrage.
    this.$store.dispatch("user/loadGid");
  },
  mounted() {
    // Charge les données de l'arbre des périmètres
    this.$store.dispatch("global/loadPerimetersTree");
  },
  components: {
    Calendar,
    searchCalendar,
    AddIndispo,
    ListUnavailabilityUser,
    ListUnavailabilityManager,
    Legends
  },
  computed: {
    ...mapState({
      numberValidation: state => state.unavailabilitiesToValidate.length
    }),
    ...mapGettersUser(["getEmployeesGid"]),
    isN1() {
      return this.getEmployeesGid.isn1 === "O";
    }
  },
  watch: {
    numberValidation() {
      this.showBadge = this.numberValidation > 0;
    }
  },
  methods: {
    setShowDate(d) {
      this.showDate = d;
    },
    closeDialog() {
      this.dialog = false;
    },
    showDialog() {
      this.dialog = true;
      this.$refs.childAddIndispo.initValue();
    },
    loadCalendar() {
      this.$refs.childCalendar.changePagination();
    },
    loadDataTableEmployeesUnavailabilities() {
      this.$refs.childDatatable.getEmployeesUnavailabilities();
    },
    loadDataTableUnavailabilitiesToValidate() {
      this.$refs.childToValidate.getUnavailabilitiesToValidate();
    },
    showStandbyTime() {
      this.visibility = this.$refs.childCalendar.showHideStandbyTime();
    }
  }
};
</script>

<style>
.list__tile:hover,
.list__tile:active {
  color: #ff9800 !important;
}

.roundicon {
  background-color: #0097c1 !important;
  color: white !important;
  border-radius: 50px !important;
  padding: 5px;
  font-size: 20px;
}

.subtitle {
  font-size: 16px;
}
</style>



