<template>
  <v-layout row wrap class="pl-3 pr-3 pb-3" >
    <v-flex xs12 >
       <v-text-field
       light
       prepend-icon="search"
       append-icon="cancel"
       :append-icon-cb="clearSearch"
       hide-details
       single-line
       :label='$t("message.search_label")'
       v-model="treeFilter"
       class="filter-field">
       </v-text-field>
    </v-flex>
    <v-flex xs12 class="mt-2" @keyup.space="debounceSave" @keyup.enter="debounceSave" v-if="treeDatas.length && getMembersSameTeams.length">
      <tree
          ref="tree"
          class="tree-highlights"
          :filter="treeFilter2"
          :options="treeOptions"
          v-model="treeModel"
          :data="treeDatas"/>
    </v-flex>
  </v-layout>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
import Events from "../Events";

const { mapActions, mapGetters } = createNamespacedHelpers("global");
const namespacedHelpersUser = createNamespacedHelpers("user");
const mapGettersUser = namespacedHelpersUser.mapGetters;

export default {
  name: "searchCalendarView",
  data() {
    return {
      // eslint-disable-next-line
      minToFilter: 2,
      debounce: null,
      debounceTimer: 750,
      treeFilter: "",
      treeModel: [],
      allUsers: [],
      treeOptions: {
        // store: {
        //   store: this.$store,
        //   key: "treeStore",
        //   mutations: ["updateTree"]
        // },
        checkbox: true,
        propertyNames: {
          text: "nomLong"
          // children: "children", Ne fonctionne pas en mode store pour le moment
          // id: "cd"
        },
        filter: {
          plainList: false,
          emptyText: "Aucune entités correspondantes",
          showChildren: false,
          matcher(query, node) {
            return query
              .split(" ")
              .every(word =>
                node.text.toUpperCase().includes(word.toUpperCase())
              );
          }
        }
      }
    };
  },
  computed: {
    ...mapGettersUser(["getMembersSameTeams", "getGid"]),
    ...mapGetters(["getAllUsers"]),
    treeFilter2() {
      // String envoyé au composant pour effectuer la recherche, retire les accents
      return this.treeFilter.length > this.minToFilter
        ? this.treeFilter.normalize("NFD").replace(/[\u0300-\u036f]/g, "")
        : null;
    },
    treeDatas() {
      // Retourne les données de l'arbre, avec les entités précochées
      const members = this.getMembersSameTeams;
      const entitiesIds = members.map(m => m.gid_collaborateur);
      const perimeters = this.$store.state.global.treeStore;
      if (perimeters.length) {
        for (let i = 0; i < perimeters.length; i += 1) {
          if (perimeters[i].children) {
            this.traverse(perimeters[i], entitiesIds);
          }
        }
      }
      return perimeters;
    }
  },
  methods: {
    ...mapActions(["loadEmployeesListFromGid"]),
    traverse(o, members) {
      // Fonction de parcours des entities de l'arbre afin de précocher les entitiés du perimetre par défaut
      let hasEmptyChilds = true;

      const option = o;
      if (members.indexOf(option.id) > -1) {
        option.state = { checked: true };
      }
      // Entite
      if (option.children) {
        for (let i = 0; i < option.children.length; i += 1) {
          this.traverse(option.children[i], members);
          hasEmptyChilds =
            hasEmptyChilds && this.traverse(option.children[i], members);
        }
      } else if (!option.lib_niv_entite) {
        hasEmptyChilds = false;
      }

      if (hasEmptyChilds) {
        option.state = Object.assign({ disabled: true }, option.state);
      }

      return hasEmptyChilds;
    },
    clearSearch() {
      // Fonction liée à l'icone de vidage du champs texte de filtrage de l'arbre
      this.treeFilter = "";
    },
    saveInputs() {
      // On récupère les utilisateurs selectionnés, et on les envois dans l'action setSelectedGids qui enregistre les Gids dans le store trié par entite nom prenom
      const selectedGids = this.treeModel.checked
        .filter(p => !p.children.length && this.getAllUsers[p.id])
        .map(p => this.getAllUsers[p.id]);

      // Met a jour le store
      this.$store.dispatch("global/setSelectedGids", selectedGids);

      // On récupère les utilisateurs selectionnés puis le composant Calendar ira chercher leurs indispos
      this.$root.$emit(Events.CALENDAR_LOAD);
    },
    debounceSave() {
      // On reset le timeout precedent
      clearTimeout(this.debounce);
      // Debounce
      this.debounce = setTimeout(() => {
        this.saveInputs();
      }, this.debounceTimer);
    }
  },
  watch: {
    treeModel(val, oldVal) {
      // Si de nouveaux noeuds ont été cochés alors on actualise le store
      if (oldVal.checked && val.checked.length !== oldVal.checked.length) {
        this.debounceSave();
      }
    }
  }
};
</script>

<style>
.tree-highlights .tree-node.matched > .tree-content {
  background: #f7f2e7;
}
.tree-node {
  font-size: 12px;
}
.tree-checkbox.checked,
.tree-checkbox.indeterminate {
  background-color: #0097c1 !important;
  border-color: #0097c1 !important;
}
</style>



