import { createNamespacedHelpers } from "vuex";
import CalendarMathMixin from "./parts/CalendarMathMixin";
import CalendarBehaviorMixin from "./parts/CalendarBehaviorMixin";
import CalendarViewHeader from "./CalendarViewHeader";
import CancelMenu from "../CancelMenu";
import UserInformationsMenu from "../UserInformationsMenu";
import Events from "../Events";
import Props from "./parts/CalendarViewProps";
import Computed from "./parts/CalendarViewComputed";
import Data from "./parts/CalendarViewData";

const { mapState, mapActions, mapGetters } = createNamespacedHelpers("global");
const namespacedHelpersUser = createNamespacedHelpers("user");
const mapActionsUser = namespacedHelpersUser.mapActions;
const mapGettersUser = namespacedHelpersUser.mapGetters;

export default {
  name: "CalendarView",
  components: { CalendarViewHeader, CancelMenu, UserInformationsMenu },
  mixins: [CalendarMathMixin, CalendarBehaviorMixin],

  props: Props,
  mounted() {
    this.init();

    this.$root.$on("input", startDate => {
      this.$root.$emit(
        Events.CALENDAR_LOAD,
        startDate,
        this.addDays(startDate, this.numberDays)
      );
    });

    this.$root.$on(
      Events.CALENDAR_LOAD,
      (
        startDate = this.periodStart,
        endDate = this.addDays(this.periodStart, this.numberDays)
      ) => {
        if (!this.selectedGids.length) {
          // Si aucun utilisateur n'a été selectionné on affiche les indispos des membres du perimetre par defaut
          this.loadMembersSameTeams(
            this.paginationCalendar,
            this.numberMembers
          ).then(a => {
            if (this.getAllGidsTeam.length) {
              this.paginationCalendarLength = Math.ceil(
                this.getAllGidsTeam.length / this.numberMembers
              );
              this.debounceLoadInformations(a, startDate, endDate);
            }
          });
        } else {
          // Sinon on affichage les utilisateurs selectionnés ainsi que leurs indispos
          this.loadSelectedMembersFromGids(
            this.paginationCalendar,
            this.numberMembers
          ).then(a => {
            if (this.selectedGids.length) {
              this.paginationCalendarLength = Math.ceil(
                this.selectedGids.length / this.numberMembers
              );
              this.debounceLoadInformations(a, startDate, endDate);
            }
          });
        }
      }
    );
    this.$root.$emit(Events.CALENDAR_LOAD);

    this.$nextTick(() => {
      window.addEventListener("resize", this.loadNumberDays);
    });
  },
  data: () => Data,
  computed: Object.assign(
    {
      ...mapGetters(["getEmployeesGid"]),
      ...mapGettersUser([
        "getMembersSameTeams",
        "getAllGidsTeam",
        "getGid",
        "getEmployeesPerimeters"
      ]),
      ...mapState({
        events: state => state.indispos,
        publicHolidays: state => state.publicHolidays,
        standbyTime: state => state.standbyTime,
        perimeters: state => state.perimeters,
        selectedGids: state => state.selectedGids,
        requestNumber: state => state.requestNumber
      }),
      filteredUser() {
        return this.filterUsers(this.getMembersSameTeams);
      }
    },
    Computed
  ),
  methods: {
    ...mapActions([
      "getUsersUnavailabilitiesSearch",
      "getUnavailabilityReasons",
      "getPublicHolidaysSearch",
      "getStandByTimeSearch",
      "loadEmployeesListFromGid",
      "incrementRequestNumber"
    ]),
    ...mapActionsUser(["loadMembersSameTeams", "loadSelectedMembersFromGids"]),
    init() {
      this.$root.$on(Events.ADD_INDISPO_LEAVE, () => {
        this.$root.$emit(Events.CALENDAR_LOAD);
      });
    },
    filterUsers(users) {
      return !this.perimeters.length ? users : this.perimeters;
    },
    debounceLoadInformations(users, startDate, endDate) {
      // On reset le timeout precedent
      clearTimeout(this.debounce);
      // Debounce
      this.debounce = setTimeout(() => {
        this.loadInformations(users, startDate, endDate);
      }, this.debounceTimer);
    },
    loadInformations(users, startDate, endDate) {
      if (users.length) {
        this.startDateLatest = startDate;
        this.getUnavailabilityReasons().then(_ => {
          this.incrementRequestNumber();

          this.getUsersUnavailabilitiesSearch({
            requestNumber: this.requestNumber,
            users,
            startDate,
            endDate
          });

          this.getPublicHolidaysSearch({
            requestNumber: this.requestNumber,
            users,
            startDate,
            endDate
          });
          this.getStandByTimeSearch({
            requestNumber: this.requestNumber,
            users,
            startDate,
            endDate
          });
        });
      }
    },
    changePagination() {
      this.$emit("input", this.startDateLatest);
      this.$root.$emit("input", this.startDateLatest);
    },
    loadNumberDays() {
      this.numberDays = window.innerWidth < 1024 ? 7 : 14;
    },
    //Fonction activant l'affichage de la popup UserInformation
    showMenuUserInformations(item, event) {
      event.preventDefault();
      //On affiche la popup UserInformationsMenu
      setTimeout(() => {
        this.$root.$emit(
          "show-menu-user",
          true,
          item,
          event.clientY,
          event.clientX
        );
      }, 0);
    },

    borderDaySelect(e) {
      const blocDay = e.toElement;

      let newIdBlocDay = blocDay.getAttribute("id");

      if (newIdBlocDay < 10) {
        newIdBlocDay = `0${newIdBlocDay}`;
      }

      if (this.idBlocDay !== newIdBlocDay) {
        const oldListe = document.getElementsByClassName(`d${this.idBlocDay}`);
        for (let i = 0; i < oldListe.length; i += 1) {
          oldListe[i].classList.remove("borderBold");
        }
      }
      this.idBlocDay = newIdBlocDay;
      const liste = document.getElementsByClassName(`d${this.idBlocDay}`);
      for (let k = 0; k < liste.length; k += 1) {
        liste[k].classList.toggle("borderBold");
      }
    },
    //Fonction d'affichage de la popup d'annulation/consultation de l'indisponibilite
    show(e) {
      const idToDelete = e.srcElement.id;
      const titleToDelete = e.srcElement.title;
      const cancelUser = this.cancelableUser(idToDelete);
      const cancelManager = this.cancelableManager(idToDelete);
      e.preventDefault();
      setTimeout(() => {
        this.$root.$emit(
          "show-menu-change",
          true,
          idToDelete,
          titleToDelete,
          e.clientX,
          e.clientY,
          cancelUser || cancelManager
        );
      }, 0);
    },
    //Fonction d'affichage des periodes d'astreinte
    showHideStandbyTime() {
      this.showStandbyTime = !this.showStandbyTime;
      return this.showStandbyTime;
    },
    //L'utilisateur peut-il annuler cette indisponibilite boolean
    cancelableUser(idEvent) {
      const evt = this.getEventFromEvents(idEvent);
      const endDate = evt.endDate;
      const user = evt.id_user;
      return this.toLocalDate(endDate) > new Date() && user === this.getGid;
    },
    //Le manager peut-il annuler cette indisponibilite boolean
    cancelableManager(idEvent) {
      const evt = this.getEventFromEvents(idEvent);
      return evt.managed;
    },

    // ******************************
    // UI Events
    // ******************************

    onClickDay(day) {
      if (this.disablePast && this.isInPast(day)) return;
      if (this.disableFuture && this.isInFuture(day)) return;
      this.$emit("click-date", day);
    },

    onClickEvent(e, day) {
      this.$emit("click-event", e, day);
    },

    onChangeDate(d) {
      this.$emit("show-date-change", d);
    },

    // ******************************
    // Drag and drop events
    // ******************************

    onDragStart(calendarEvent, windowEvent) {
      if (!this.enableDragDrop) return false;
      // Not using dataTransfer.setData to store the event ID because it (a) doesn't allow access to the data being
      // dragged during dragover, dragenter, and dragleave events, and because storing an ID requires an unnecessary
      // lookup. This does limit the drop zones to areas within this instance of this component.
      this.currentDragEvent = calendarEvent;
      // Firefox and possibly other browsers require dataTransfer to be set, even if the value is not used. IE11
      // requires that the first argument be exactly "text" (not "text/plain", etc.).
      windowEvent.dataTransfer.setData("text", "foo");
      this.$emit("drag-start", calendarEvent);
      return true;
    },

    handleDragEvent(bubbleEventName, bubbleParam) {
      if (!this.enableDragDrop) return false;
      if (!this.currentDragEvent) {
        // shouldn't happen
        // If current drag event is not set, check if user has set its own slot for events
        if (!this.$scopedSlots.event) return false;
      }
      this.$emit(bubbleEventName, this.currentDragEvent, bubbleParam);
      return true;
    },

    onDragOver(day) {
      this.handleDragEvent("drag-over-date", day);
    },

    onDragEnter(day, windowEvent) {
      if (!this.handleDragEvent("drag-enter-date", day)) return;
      windowEvent.target.classList.add("draghover");
    },

    onDragLeave(day, windowEvent) {
      if (!this.handleDragEvent("drag-leave-date", day)) return;
      windowEvent.target.classList.remove("draghover");
    },

    onDrop(day, windowEvent) {
      if (!this.handleDragEvent("drop-on-date", day)) return;
      windowEvent.target.classList.remove("draghover");
    }
  }
};



