<template>
	<v-menu v-model="showCancelMenu" offset-y absolute :position-x="x" :position-y="y" :close-on-content-click="false">
    <v-card>
      <v-card-title>
          <v-icon class="roundicon  mr-2">error_outline</v-icon>{{title}}
      </v-card-title>
      <v-divider></v-divider>
      <v-card-actions>
        <v-btn small color="grey" @click.stop="showCancelMenu = false">{{$t("message.cancel")}}</v-btn>
        <v-btn v-if="cancelable" small color="primary" dark  @click="confirmDelete(idToDelete)"><v-icon>delete</v-icon>{{$t("message.delete")}}</v-btn>
      </v-card-actions>
    </v-card>
  </v-menu>
</template>

<script>
import { createNamespacedHelpers } from "vuex";

const { mapActions } = createNamespacedHelpers("global");

export default {
  name: "CancelMenu",
  data: () => ({
    x: 0,
    y: 0,
    showCancelMenu: false,
    idToDelete: null,
    title: null,
    cancelable: null
  }),
  mounted() {
    this.$root.$on(
      "show-menu-change",
      (showCancelMenu, idToDelete, title, x, y, cancelable) => {
        this.showCancelMenu = showCancelMenu;
        this.idToDelete = idToDelete;
        this.title = title;
        this.x = x - 150;
        this.y = y + 25;
        this.cancelable = cancelable;
      }
    );
  },
  methods: {
    ...mapActions(["deleteUnavailabilities"]),
    confirmDelete(idToDelete) {
      this.$swal({
        title: `${this.$t("message.delete")} ?`,
        text: this.title,
        focusConfirm: false,
        showCancelButton: true,
        confirmButtonClass: "btn primary btnsmall",
        cancelButtonClass: "btn btnsmall",
        cancelButtonText: this.$t("message.no"),
        confirmButtonText: this.$t("message.yes"),
        reverseButtons: true
      }).then(result => {
        if (result.value) {
          this.$swal({
            title: this.$t("message.finish"),
            text: "L'indisponibilité a bien été supprimée",
            confirmButtonClass: "btn primary btnsmall",
            confirmButtonText: this.$t("message.OK")
          });
          this.deleteUnavailabilities(idToDelete);
          this.showCancelMenu = false;
        }
      });
    }
  }
};
</script>

<style>
.btnsmall {
  padding: 0 8px !important;
  font-size: 13px !important;
  height: 28px !important;
}

.swal2-title {
  font-size: 25px !important;
}
.swal2-content {
  font-size: 15px !important;
  font-weight: normal !important;
}
</style>



