const dayWidthFor14DaysDisplay = 101.13;
const dayWidthFor7DaysDisplay = 74.86;

export default {
  methods: {
    getEventFromEvents(idEvent) {
      return this.events.filter(evt => evt.id === +idEvent)[0];
    },
    // ******************************
    // Calendar Events
    // ******************************

    findAndSortEventsInWeek(weekStart, user) {
      // Return a list of events that INCLUDE any day of a week starting on a
      // particular day. Sorted so the events that start earlier are always
      // shown first.
      const events = this.fixedEvents
        .filter(
          event =>
            event.startDate < this.addDays(weekStart, this.numberDays) &&
            event.endDate >= weekStart &&
            event.id_user === user.gid_collaborateur,
          this
        )
        .sort((a, b) => {
          if (a.startDate < b.startDate) return -1;
          if (b.startDate < a.startDate) return 1;
          if (a.endDate > b.endDate) return -1;
          if (b.endDate > a.endDate) return 1;
          return a.id < b.id ? -1 : 1;
        });

      const astreintes = this.standbyTime[user.gid_collaborateur];
      if (astreintes)
        astreintes
          .filter(
            ast =>
              new Date(ast.start_date) <
                this.addDays(weekStart, this.numberDays) &&
              new Date(ast.end_date) >= weekStart
          )
          .forEach(ast => {
            events.unshift({
              startDate: new Date(ast.start_date),
              endDate: new Date(ast.end_date),
              classes: [],
              astreinte: true
            });
          });

      return events;
    },

    getWeekEvents(weekStart, user) {
      // Return a list of events that CONTAIN the week starting on a day.
      // Sorted so the events that start earlier are always shown first.
      const events = this.findAndSortEventsInWeek(weekStart, user);
      const results = [];
      // eslint-disable-next-line
      const eventRows = Array(24 * this.numberDays)
        .fill(0)
        // eslint-disable-next-line
        .map(_ => []);
      // eslint-disable-next-line
      for (let i = 0; i < events.length; i++) {
        const ep = Object.assign({}, events[i], {
          classes: [...events[i].classes],
          eventRow: 0
        });
        const continued = ep.startDate < weekStart;
        // Dans l'affichage la largeur d'une journée (AM/PM) correspond à deux heures dans l'affichage par heure.
        const dayWidth =
          this.numberDays === 14
            ? dayWidthFor14DaysDisplay
            : dayWidthFor7DaysDisplay;
        const coeffEcran = this.showHour ? 1 : dayWidth / 24 / this.hourWidth;
        const startOffset = continued
          ? 0
          : this.hourDiff(weekStart, ep.startDate) * coeffEcran;
        ep.startOffset = startOffset;
        const endDate = this.addDays(this.periodStart, this.numberDays);
        const span =
          Math.min(
            168 * 2 - startOffset,
            this.hourDiff(
              ep.startDate < weekStart ? weekStart : ep.startDate,
              ep.endDate < endDate ? ep.endDate : endDate
            )
          ) * coeffEcran;

        ep.span = span;

        if (!ep.id) ep.classes.push("astreinte");
        if (continued) ep.classes.push("continued");
        if (this.dayDiff(weekStart, ep.endDate) > this.numberDays)
          ep.classes.push("toBeContinued");
        if (this.isInPast(ep.endDate)) ep.classes.push("past");
        if (ep.originalEvent && ep.originalEvent.url) ep.classes.push("hasUrl");
        // eslint-disable-next-line
        for (let d = 0; d < this.numberDays; d++) {
          if (d === startOffset) {
            let s = 0;
            // eslint-disable-next-line
            while (eventRows[d][s]) s++;
            ep.eventRow = s;
            eventRows[d][s] = true;
          } else if (d < startOffset + span) {
            eventRows[d][ep.eventRow] = true;
          }
        }
        results.push(ep);
      }

      return results;
    },
    /*
    Creates the HTML to prefix the event title showing the event's start and/or
    end time. Midnight is not displayed.
    */
    getFormattedTimeRange(e) {
      const startTime = this.formattedTime(
        e.startDate,
        this.displayLocale,
        this.timeFormatOptions
      );
      let endTime = "";
      if (!this.isSameDateTime(e.startDate, e.endDate)) {
        endTime = this.formattedTime(
          e.endDate,
          this.displayLocale,
          this.timeFormatOptions
        );
      }
      return (
        (startTime !== ""
          ? `<span class="startTime">${startTime}</span>`
          : "") +
        (endTime !== "" ? `<span class="endTime">${endTime}</span>` : "")
      );
    },

    getEventTitle(e) {
      if (!this.showEventTimes) return e.title;
      return `${this.getFormattedTimeRange(e)} ${e.title}`;
    },

    getEventTop(e) {
      // Compute the top position of the event based on its assigned row within the given week.
      const r = e.eventRow;
      const h = this.eventContentHeight;
      const b = this.eventBorderHeight;
      return `calc(${this.eventTop} + ${r}*${h} + ${r}*${b})`;
    },

    getEventLeft(e) {
      return `calc((${this.hourWidth * e.startOffset}px))`;
    },
    getEventWidth(e) {
      return `${this.hourWidth * e.span}px`;
    },

    // ******************************
    // Date Periods
    // ******************************

    /*
    Returns a date for the current display date moved forward or backward by a given
    number of the current display units. Returns null if said move would result in a
    disallowed display period.
    */
    getIncrementedPeriod(count) {
      const newStartDate = this.incrementPeriod(
        this.periodStart,
        this.displayPeriodUom,
        count
      );
      const newEndDate = this.incrementPeriod(
        newStartDate,
        this.displayPeriodUom,
        this.displayPeriodCount
      );
      if (this.disablePast && newEndDate <= this.today()) return null;
      if (this.disableFuture && newStartDate > this.today()) return null;
      return newStartDate;
    }
  }
};



