
<template>
	<div class="cv-header white">
    <v-layout row wrap align-center>
      <v-flex xs12 align-center>
        <div class="periodLabel">
			    <slot name="label"/>
	    	</div>
      </v-flex>
    </v-layout>
    <v-layout row wrap align-center>
      <v-flex xs12 text-xs-center>
        <div class="cv-header-nav">
			    <v-btn fab dark small color="grey lighten-1" :disabled="!headerProps.previousMonth" class="previousMonth" @click="onInput(headerProps.previousMonth)"><v-icon>first_page</v-icon></v-btn>
			    <v-btn fab dark small color="grey lighten-1" :disabled="!headerProps.previousPeriod" class="previousPeriod" @click="onInput(headerProps.previousPeriod)"><v-icon>chevron_left</v-icon></v-btn>
          <v-btn  dark small color="primary" class="currentPeriod" @click="onInput(headerProps.currentPeriod)">Semaine en cours</v-btn>
			    <v-btn fab dark small color="grey lighten-1" :disabled="!headerProps.nextPeriod" class="nextPeriod" @click="onInput(headerProps.nextPeriod)"><v-icon>chevron_right</v-icon></v-btn>
			    <v-btn fab dark small color="grey lighten-1" :disabled="!headerProps.nextMonth" class="nextMonth" @click="onInput(headerProps.nextMonth)"><v-icon>last_page</v-icon></v-btn>
				</div>
      </v-flex>
    </v-layout>
	</div>
</template>

<script>
export default {
  name: "CalendarViewHeader",
  props: {
    headerProps: {
      type: Object,
      required: true
    }
  },
  methods: {
    onInput(d) {
      this.$emit("input", d);
      this.$root.$emit("input", d);
    }
  }
};
</script>
<style>
.cv-header {
  min-height: 2.5em;
  border-width: 1px 1px 0 1px;
}

.cv-header .periodLabel {
  min-height: 1.5em;
  line-height: 1;
  font-size: 1.5em;
}

.cv-header-nav,
.cv-header .periodLabel {
  margin: 0.1em 0.6em;
  margin: auto;
  text-align: center;
}

.periodLabel {
  margin: auto;
}
</style>



